import uniAdd from '../../components/uni-add/uni-add.vue';
export default {
	components: {
		uniAdd
	},
	data() {
		return {
			userList: 10
		};
	},
	methods: {
		addBtnClick() {
			uni.navigateTo({
				url: `/pages/goodsAdd/goodsAdd`
			});
		},
		handleClickDetails(id) {
			uni.navigateTo({
				url: `/pages/goodsDetails/goodsDetails?id=${id}`
			});
		}
	}
};
